"use client";
import React from "react";
import { useNavigate } from "react-router-dom";
import { CardBody, CardContainer, CardItem } from "./ui/3d-card";
import Imagenrino from "../img/Rinoplastia_CARD.png";
import ImagenMamas from "../img/AumentodeMamasCard.jpg";
import ImagenLipo from "../img/Liposuccion_Card.png";

/**
 * ThreeDCardDemo.jsx
 * - Cada CardContainer recibe className="card3d"
 * - onMouseDown/onMouseUp agregan/quitan .card--pressed para feedback inmediato
 */
export default function ThreeDCardDemo() {
  const navigate = useNavigate();

  const goToProcedure = (slug) => {
    navigate(`/procedimiento/${slug}`);
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  const handlePressStart = (e) => {
    const root = e.currentTarget.closest(".card3d");
    if (root) root.classList.add("card--pressed");
  };
  const handlePressEnd = (e) => {
    const root = e.currentTarget.closest(".card3d");
    if (root) root.classList.remove("card--pressed");
  };

  const imgStyle = {
    width: "100%",
    height: 220,
    objectFit: "cover",
    display: "block",
    borderRadius: "var(--radius-xl)",
    opacity: 0,
    transition: "opacity .35s ease",
  };

  const onImgLoad = (e) => { e.currentTarget.style.opacity = 1; };

  return (
    <div
      style={{
        display: "grid",
        gap: "1.25rem",
        gridTemplateColumns: "repeat(auto-fit, minmax(260px, 1fr))",
      }}
    >
      {/* Card 1: Rinoplastia */}
      <CardContainer className="card3d">
        <CardBody className="is-dark" style={{ padding: "1.25rem", minHeight: "360px" }}>
          <CardItem translateZ="55" className="h3" style={{ marginBottom: ".35rem" }}>
            Rinoplastia
          </CardItem>

          <CardItem as="p" translateZ="70" className="muted" style={{ maxWidth: "48ch" }}>
            Mejore la forma y funcin de su nariz con resultados naturales y armnicos.
          </CardItem>

          <CardItem translateZ="110" style={{ marginTop: "1rem" }}>
            <div
              className="card__media"
              style={{ borderRadius: "var(--radius-xl)", cursor: "pointer" }}
              onClick={() => goToProcedure("rinoplastia")}
              onMouseDown={handlePressStart}
              onMouseUp={handlePressEnd}
              onMouseLeave={handlePressEnd}
              role="button"
              aria-label="Abrir detalle de Rinoplastia"
            >
              <img
                src={Imagenrino}
                alt="Rinoplastia - Ecuador Aesthetics"
                className="Imagenes_procediminetos"
                style={imgStyle}
                onLoad={onImgLoad}
              />
            </div>
          </CardItem>

          <div
            style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginTop: "1.25rem" }}
          >
            <CardItem
              translateZ={25}
              as="a"
              href="#procedimientos"
              className="card__link"
              onClick={(e) => {
                e.preventDefault();
                goToProcedure("rinoplastia");
              }}
            />
            <CardItem
              translateZ={25}
              as="button"
              className="btn btn--ghost"
              onClick={(e) => {
                e.stopPropagation();
                goToProcedure("rinoplastia");
              }}
              onMouseDown={handlePressStart}
              onMouseUp={handlePressEnd}
              onMouseLeave={handlePressEnd}
              aria-label="Ver ms sobre Rinoplastia"
            >
              Ver ms
            </CardItem>
          </div>
        </CardBody>
      </CardContainer>

      {/* Card 2: Aumento de Mamas */}
      <CardContainer className="card3d">
        <CardBody className="is-dark" style={{ padding: "1.25rem", minHeight: "360px" }}>
          <CardItem translateZ="55" className="h3" style={{ marginBottom: ".35rem" }}>
            Aumento de Mamas
          </CardItem>

          <CardItem as="p" translateZ="70" className="muted" style={{ maxWidth: "48ch" }}>
            Logre el volumen y la forma deseada para sus senos con implantes de alta calidad.
          </CardItem>

          <CardItem translateZ="110" style={{ marginTop: "1rem" }}>
            <div
              className="card__media"
              style={{ borderRadius: "var(--radius-xl)", cursor: "pointer" }}
              onClick={() => goToProcedure("aumento-de-mamas")}
              onMouseDown={handlePressStart}
              onMouseUp={handlePressEnd}
              onMouseLeave={handlePressEnd}
              role="button"
              aria-label="Abrir detalle de Aumento de Mamas"
            >
              <img
                src={ImagenMamas}
                alt="Aumento de Mamas - Ecuador Aesthetics"
                className="Imagenes_procediminetos"
                style={imgStyle}
                onLoad={onImgLoad}
              />
            </div>
          </CardItem>

          <div
            style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginTop: "1.25rem" }}
          >
            <CardItem
              translateZ={25}
              as="a"
              href="#doctor"
              className="card__link"
              onClick={(e) => {
                e.preventDefault();
                goToProcedure("aumento-de-mamas");
              }}
            />
            <CardItem
              translateZ={25}
              as="button"
              className="btn btn--ghost"
              onClick={(e) => {
                e.stopPropagation();
                goToProcedure("aumento-de-mamas");
              }}
              onMouseDown={handlePressStart}
              onMouseUp={handlePressEnd}
              onMouseLeave={handlePressEnd}
              aria-label="Ver ms sobre Aumento de Mamas"
            >
              Ver ms
            </CardItem>
          </div>
        </CardBody>
      </CardContainer>

      {/* Card 3: Liposuccin */}
      <CardContainer className="card3d">
        <CardBody className="is-dark" style={{ padding: "1.25rem", minHeight: "360px" }}>
          <CardItem translateZ="55" className="h3" style={{ marginBottom: ".35rem" }}>
            Liposuccin
          </CardItem>

          <CardItem as="p" translateZ="70" className="muted" style={{ maxWidth: "48ch" }}>
            Elimine depsitos de grasa localizados y esculpa su figura de manera efectiva.
          </CardItem>

          <CardItem translateZ="110" style={{ marginTop: "1rem" }}>
            <div
              className="card__media"
              style={{ borderRadius: "var(--radius-xl)", cursor: "pointer" }}
              onClick={() => goToProcedure("liposuccion")}
              onMouseDown={handlePressStart}
              onMouseUp={handlePressEnd}
              onMouseLeave={handlePressEnd}
              role="button"
              aria-label="Abrir detalle de Liposuccin"
            >
              <img
                src={ImagenLipo}
                alt="Liposuccin - Ecuador Aesthetics"
                className="Imagenes_procediminetos"
                style={imgStyle}
                onLoad={onImgLoad}
              />
            </div>
          </CardItem>

          <div
            style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginTop: "1.25rem" }}
          >
            <CardItem
              translateZ={25}
              as="a"
              href="#testimonios"
              className="card__link"
              onClick={(e) => {
                e.preventDefault();
                goToProcedure("liposuccion");
              }}
            />
            <CardItem
              translateZ={25}
              as="button"
              className="btn btn--ghost"
              onClick={(e) => {
                e.stopPropagation();
                goToProcedure("liposuccion");
              }}
              onMouseDown={handlePressStart}
              onMouseUp={handlePressEnd}
              onMouseLeave={handlePressEnd}
              aria-label="Ver ms sobre Liposuccin"
            >
              Ver ms
            </CardItem>
          </div>
        </CardBody>
      </CardContainer>
    </div>
  );
}
