"use client";
import React, { useState, useEffect, useRef } from "react";
import {
  Info, ClipboardList, HeartPulse, ShieldAlert, WalletMinimal,
  ChevronDown
} from "lucide-react";

/* Categoras + conos */
const CATEGORIES = [
  { key: "general",  label: "Preguntas Generales",        icon: Info },
  { key: "pre",      label: "Pre-ciruga",                 icon: ClipboardList },
  { key: "post",     label: "Post-ciruga y Recuperacin", icon: HeartPulse },
  { key: "riesgos",  label: "Riesgos y Seguridad",         icon: ShieldAlert },
  { key: "costos",   label: "Costos y Financiacin",       icon: WalletMinimal },
];

/* Preguntas por categora (solo contenido, no lo toqu) */
const QA = {
  general: [
    {
      q: "Cul es el primer paso para considerar una ciruga esttica?",
      a: "Agendar una consulta con el especialista. En esa cita se evalan objetivos, antecedentes y se propone un plan personalizado con opciones y expectativas realistas."
    },
    {
      q: "Cmo s qu procedimiento es el adecuado para m?",
      a: "Tras un examen clnico y conversar sobre tus metas, el cirujano explica alternativas, beneficios, lmites y recuperacin de cada opcin para una decisin informada."
    },
    {
      q: "Son permanentes los resultados de la ciruga esttica?",
      a: "Muchos resultados son duraderos, pero el envejecimiento y cambios de peso influyen. Mantener hbitos saludables ayuda a preservar los efectos a largo plazo."
    },
  ],
  pre: [
    { q: "Qu estudios necesito antes de la ciruga?",
      a: "Depende del procedimiento y tu historia clnica, pero suelen incluir laboratorio bsico, evaluacin cardiolgica y, si corresponde, imgenes especficas." },
    { q: "Debo suspender medicacin o suplementos?",
      a: "Siempre inform todo lo que toms. Algunos frmacos/antiinflamatorios/suplementos se suspenden para reducir riesgo de sangrado." },
  ],
  post: [
    { q: "Cmo es el dolor y cunto dura la recuperacin?",
      a: "El dolor suele ser controlable con medicacin. La recuperacin inicial vara entre 1 y 3 semanas segn el procedimiento; las restricciones se explican por escrito." },
    { q: "Cundo puedo retomar el ejercicio?",
      a: "Actividad suave a la 1-2 semana si tu cirujano lo aprueba; entrenamiento de fuerza o alto impacto suele reanudarse gradualmente despus de 4-6 semanas." },
  ],
  riesgos: [
    { q: "Qu riesgos existen y cmo se minimizan?",
      a: "Toda ciruga implica riesgos (sangrado, infeccin, cicatrizacin). Se reducen con una buena evaluacin preoperatoria, tcnica adecuada y seguimiento cercano." },
  ],
  costos: [
    { q: "Qu incluye un presupuesto?",
      a: "Honorarios mdicos, gastos de quirfano, internacin, materiales y controles. Se detalla por escrito para evitar sorpresas." },
    { q: "Ofrecen financiacin?",
      a: "Segn el caso, podemos ofrecer planes y medios de pago. Consult durante la evaluacin para opciones vigentes." },
  ],
};

export default function FAQ() {
  const [cat, setCat] = useState("general");
  const [open, setOpen] = useState(0); // ndice abierto en la lista actual
  const sectionRef = useRef(null);

  // reveal del bloque al entrar en viewport (aprovecha tus estilos .is-inview)
  useEffect(() => {
    const node = sectionRef.current;
    if (!node || !("IntersectionObserver" in window)) return;
    const io = new IntersectionObserver((entries) => {
      entries.forEach((e) => e.isIntersecting && node.classList.add("is-inview"));
    }, { threshold: 0.28, rootMargin: "0px 0px -10% 0px" });
    io.observe(node);
    return () => io.disconnect();
  }, []);

  const items = QA[cat] ?? [];
  const toggle = (i) => setOpen(open === i ? -1 : i);

  return (
    <section ref={sectionRef} id="faq" className="section faq marked">
      <div className="container">
        <h2 className="faq__title">Encuentra tus Respuestas</h2>
        <p className="faq__subtitle">Navega por las categoras para resolver tus dudas.</p>

        <div className="faq__layout">
          {/* Lateral */}
          <nav className="faq-nav" aria-label="Categoras de preguntas">
            {CATEGORIES.map(({ key, label, icon: Icon }) => (
              <button
                key={key}
                className={`faq-nav__btn ${cat === key ? "is-active" : ""}`}
                onClick={() => { setCat(key); setOpen(0); }}
                type="button"
              >
                <span className="faq-nav__icon"><Icon size={18} /></span>
                <span className="faq-nav__label">{label}</span>
              </button>
            ))}
          </nav>

          {/* Acorden */}
          <div className="faq-acc" role="region" aria-label="Listado de preguntas">
            {items.map((it, i) => {
              const expanded = open === i;
              return (
                <div
                  key={i}
                  className={`faq-acc__item ${expanded ? "is-open" : ""}`}
                  style={{ "--stagger": `${i * 90}ms` }}
                >
                  <button
                    className="faq-acc__q"
                    onClick={() => toggle(i)}
                    aria-expanded={expanded}
                    type="button"
                  >
                    <span>{it.q}</span>
                    <ChevronDown className="faq-acc__chev" size={18} aria-hidden />
                  </button>

                  {/* Siempre renderizado para poder animar altura */}
                  <div className="faq-acc__a" aria-hidden={!expanded}>
                    <div className="faq-acc__a-inner">
                      <p>{it.a}</p>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
}
