// src/App.js
import React from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";

import "./App.css";

import PageLoader from "./components/ui/PageLoader";
import ScrollToTop from "./components/ScrollToTop";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import Hero from "./components/Hero";
import About from "./components/About";
import Testimonials from "./components/Testimonials";
import CtaIA from "./components/CtaIA";
import ThreeDCardDemo from "./components/ThreeDCardDemo";
import FAQ from "./components/FAQ";
import SectionScrollCues from "./components/SectionScrollCues";
import FinalSection from "./components/FinalSection"; //  NUEVO

// Lazy pages
const ProceduresPageLazy = React.lazy(() => import("./components/Procedures"));
const ProcedureDetailPageLazy = React.lazy(() =>
  import("./components/ProcedureDetail")
);
const GalleryPageLazy = React.lazy(() => import("./components/Gallery"));
const ConsultationPageLazy = React.lazy(() =>
  import("./components/ConsultationAI")
);
const ContactPageLazy = React.lazy(() => import("./components/Contact"));

function Home() {
  return (
    <main tabIndex={-1}>
      <SectionScrollCues />

      {/* Fade-in secciones */}
      <style>{`
        .section.marked{
          opacity:0;
          transform: translateY(12px);
          transition: opacity .45s ease, transform .45s ease;
          will-change: opacity, transform;
        }
        .section.marked.is-inview{
          opacity:1;
          transform:none;
        }
      `}</style>

      {/* HERO */}
      <section className="section marked" style={{ padding: 0 }}>
        <Hero />
      </section>
      <br />
      <br />
      <br />

      {/*  Renombrado para evitar choque con la ruta /procedimientos */}
      <section id="procedimientos-home" className="section procs marked">
        <div className="container">
          <header className="procs__head">
            <h2 className="h2">Procedimientos Destacados</h2>
            <p className="procs__sub">
              Ofrecemos una gama de procedimientos estticos diseados para
              realzar su belleza natural con resultados excepcionales.
            </p>
          </header>

          <section className="section">
            <div className="container">
              <ThreeDCardDemo />
            </div>
          </section>
        </div>
      </section>

      {/* ABOUT */}
      <section id="doctor" className="section about marked">
        <div className="container">
          <About />
        </div>
      </section>

      {/* FAQ */}
      <section className="section marked" style={{ padding: 0 }}>
        <FAQ />
      </section>

      {/* TESTIMONIOS */}
      <section id="testimonios" className="section testis marked">
        <div className="container">
          <Testimonials />
        </div>
      </section>

      {/* CTA IA */}
      <section id="consulta-ia" className="section cta marked">
        <div className="container">
          <CtaIA />
        </div>
      </section>

      {/* SECCIN FINAL (VIDEO + AGENDA TU CONSULTA) */}
      <section id="seccion-final" className="section marked" style={{ padding: 0 }}>
        <FinalSection />
      </section>
    </main>
  );
}

function SuspenseBoundary({ children }) {
  return (
    <React.Suspense fallback={<div style={{ padding: 24 }}>Cargando</div>}>
      {children}
    </React.Suspense>
  );
}

export default function App() {
  return (
    <PageLoader minDelay={700}>
      <Router>
        <ScrollToTop behavior="smooth" />
        <Navbar />

        <Routes>
          {/* Home */}
          <Route path="/" element={<Home />} />
          <Route path="/inicio" element={<Home />} />

          {/* Procedimientos (lista) */}
          <Route
            path="/procedimientos"
            element={
              <SuspenseBoundary>
                <ProceduresPageLazy />
              </SuspenseBoundary>
            }
          />
          {/* Alias opcional por si tens links /procedures */}
          <Route
            path="/procedures"
            element={
              <SuspenseBoundary>
                <ProceduresPageLazy />
              </SuspenseBoundary>
            }
          />

          {/* Detalle */}
          <Route
            path="/procedimiento/:slug"
            element={
              <SuspenseBoundary>
                <ProcedureDetailPageLazy />
              </SuspenseBoundary>
            }
          />

          {/* Galera */}
          <Route
            path="/galeria"
            element={
              <SuspenseBoundary>
                <GalleryPageLazy />
              </SuspenseBoundary>
            }
          />

          {/* Consulta IA */}
          <Route
            path="/consulta-ia"
            element={
              <SuspenseBoundary>
                <ConsultationPageLazy />
              </SuspenseBoundary>
            }
          />

          {/* Contacto */}
          <Route
            path="/contacto"
            element={
              <SuspenseBoundary>
                <ContactPageLazy />
              </SuspenseBoundary>
            }
          />

          {/* Fallback */}
          <Route path="*" element={<Home />} />
        </Routes>

        <Footer />
      </Router>
    </PageLoader>
  );
}
