// src/components/Hero.jsx
"use client";
import React, { useEffect, useRef } from "react";
import HoverBorderGradient from "./ui/hover-border-gradient";
import video from "../img/Video-explicativo.mp4";
import SplitText from "./SplitText";

export default function Hero() {
  const videoRef = useRef(null);

  useEffect(() => {
    const el = videoRef.current;
    if (!el) return;

    const io = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          el.classList.add("is-visible");
          io.unobserve(el);
        }
      },
      { threshold: 0.35, rootMargin: "0px 0px -10% 0px" }
    );

    io.observe(el);
    return () => io.disconnect();
  }, []);

  return (
    <section id="inicio" className="section hero">
      <div className="container hero__grid">
        
        {/* BLOQUE DE TEXTO */}
        <div className="hero__content">

          {/* TTULO ANIMADO */}
          <SplitText
            text="Ciruga plstica facial con precisin y criterio esttico"
            className="h1 hero__title"
            delay={80}
            duration={0.55}
            ease="power3.out"
            splitType="chars"
            from={{ opacity: 0, y: 40 }}
            to={{ opacity: 1, y: 0 }}
            threshold={0.2}
            rootMargin="-60px"
            textAlign="left"
          />

          {/* SUBTTULO ANIMADO */}
          <SplitText
            text="Ciencia + arte, trato humano y resultados naturales. Quito, Ecuador."
            className="lead hero__sub"
            delay={40}
            duration={0.5}
            ease="power2.out"
            splitType="chars"
            from={{ opacity: 0, y: 20 }}
            to={{ opacity: 1, y: 0 }}
            threshold={0.2}
            rootMargin="-60px"
            textAlign="left"
          />

          <div className="hero__actions" style={{ display: "flex", flexWrap: "wrap", gap: "1rem" }}>
            <HoverBorderGradient as="a" href="#contacto" className="hbg--slow">
              Agenda tu consulta
            </HoverBorderGradient>

            <HoverBorderGradient as="a" href="#testimonios" className="hbg--light">
              Ver testimonios
            </HoverBorderGradient>
          </div>

          <p>Procedimientos Destacados.</p>

          <div className="hero__chips chips">
            <HoverBorderGradient as="a" href="#/procedimiento/rinoplastia" className="hbg--light">
              Rinoplastia
            </HoverBorderGradient>
            <HoverBorderGradient as="a" href="#/procedimiento/aumento-de-mamas" className="hbg--light">
              Aumento de Mamas
            </HoverBorderGradient>
            <HoverBorderGradient as="a" href="#/procedimiento/liposuccion" className="hbg--light">
              Liposuccion 
            </HoverBorderGradient>
          </div>
        </div>
       
        {/* VIDEO EXPLICATIVO */}
        <aside className="hero__media">
          <video
            loading="lazy"
            ref={videoRef}
            src={video}
            autoPlay
            loop
            muted
            playsInline
            className="hero__video reveal-video"
          />
        </aside>
      </div>
    </section>
  );
}
