import React from "react";
import HoverBorderGradient from "./ui/hover-border-gradient";
import Imagen_About from "../img/About_me.jpg";

export default function About() {
  return (
    <div className="about__grid">
      <figure className="about__photo">
        <img src={Imagen_About} alt="imagen_about" className="imagen_about" />
      </figure>

      <div className="about__content">
        <h2 className="h2">Conozca al Dr. Orlando Santilln</h2>
        <p className="about__p">
         Soy el Dr. Orlando Santilln, un cirujano plstico certificado con ms de 15 aos de experiencia, reconocido por mi enfoque artstico y por mi compromiso con la seguridad y la satisfaccin de mis pacientes.
        </p>
        <p className="about__p">
          Graduado con honores de la Universidad Central del Ecuador y con
          especializaciones en Brasil y Estados Unidos, combina tcnicas
          avanzadas con un entendimiento profundo de la anatoma y la esttica
          para lograr resultados armnicos y naturales.
        </p>

        <div className="about__cta">
          <HoverBorderGradient
                        as="a"
                        href="/#contacto"
                        className="hbg--light"
                      >
            Solicitar consulta
          </HoverBorderGradient>
        </div>
      </div>
    </div>
  );
}
