"use client";
import React, { useEffect, useState } from "react";
import logoDark from "../img/Recurso 2.png";
import logoLight from "../img/Recurso 1.png";
import logo2Dark from "../img/Recurso 9.png";
import logo2Light from "../img/Recurso 8.png";
import HoverBorderGradient from "./ui/hover-border-gradient";
import ThemeToggle from "./ui/ThemeToggle";
import { Link } from "react-router-dom";

export default function Navbar() {
  const [hidden, setHidden] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const [lastY, setLastY] = useState(0);
  const [theme, setTheme] = useState("dark");

  // Detectar tema inicial y escuchar cambios de tema
  useEffect(() => {
    const initial =
      (typeof window !== "undefined" &&
        window.localStorage.getItem("theme")) ||
      document.documentElement.getAttribute("data-theme") ||
      "dark";

    setTheme(initial);

    const onTheme = (e) => {
      const newTheme =
        e?.detail?.theme ||
        document.documentElement.getAttribute("data-theme") ||
        "dark";
      setTheme(newTheme);
    };

    window.addEventListener("themeChange", onTheme);
    return () => window.removeEventListener("themeChange", onTheme);
  }, []);

  // Mostrar / ocultar navbar al hacer scroll
  useEffect(() => {
    const THRESHOLD = 10;
    const HIDE_OFFSET = 120;

    const onScroll = () => {
      const y = window.scrollY || 0;
      setScrolled(y > 8);

      if (Math.abs(y - lastY) <= THRESHOLD) return;
      if (y > lastY && y > HIDE_OFFSET) setHidden(true);
      else setHidden(false);
      setLastY(y);
    };

    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, [lastY]);

  // Elegir logos segn tema actual
  const currentLogo = theme === "light" ? logoLight : logoDark;
  const currentLogo2 = theme === "light" ? logo2Light : logo2Dark;

  return (
    <header
      className={[
        "navbar",
        hidden ? "navbar--hidden" : "navbar--visible",
        scrolled ? "navbar--scrolled" : "",
      ].join(" ")}
    >
      <div className="container navbar__wrap navbar__wrap--wide">
        {/* BRAND -> ahora es Link al Home */}
        <Link to="/inicio" className="navbar__brand" aria-label="Ir al inicio">
          <img src={currentLogo} alt="Ecuador Esttica" className="logo" />
          <img src={currentLogo2} alt="logo2" className="logo2" />
        </Link>

        <nav className="navbar__menu">
          <Link to="/inicio" className="navbar__link">
            Inicio
          </Link>
          <Link to="/procedimientos" className="navbar__link">
            Procedimientos
          </Link>
          <Link to="/galeria" className="navbar__link">
            Galera
          </Link>
          <Link to="/consulta-ia" className="navbar__link">
            Consulta IA
          </Link>
          <Link to="/contacto" className="navbar__link">
            Contacto
          </Link>

          <ThemeToggle className="navbar__toggle" />

          <div className="navbar__cta">
            <HoverBorderGradient as="a" href="#contacto">
              Agendar Cita
            </HoverBorderGradient>
          </div>
        </nav>
      </div>
    </header>
  );
}
